<template>
  <div class="classlist">
    <div class="lits_top">
      <h3>Jane Coffe</h3>
    </div>

    <!-- 轮播 -->
    <div class="swipe">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(item,index) in bannerData" :key="index">
          <img :src="item.bannerImg" alt />
        </van-swipe-item>
      </van-swipe>
    </div>
    <!-- 轮播 end-->

    <!-- 列表 -->
    <div class="list clearfix">
      <van-sticky>
        <div class="tab pr">
          <van-row type="flex" justify="center">
            <van-col
              span="6"
              v-for="(item, index) in typeData"
              :key="index"
              @click="toggleType(item, index, $event)"
            >{{item.typeDesc}}</van-col>
          </van-row>
          <div class="line pa" :style="{left: left + 'px'}"></div>
        </div>
      </van-sticky>

      <div class="shop_list">
        <div
          class="coffes_list"
          v-for="(item, index) in currentProduct"
          :key="index"
          @click="goDetail(item.pid)"
        >
          <van-row type="flex" justify="center">
            <van-col span="10">
              <img :src="item.smallImg" alt />
            </van-col>
            <van-col span="10" offset="1">
              <h3>{{item.name}}</h3>
              <p>{{item.enname}}</p>
              <span>￥{{item.price}}</span>
            </van-col>
          </van-row>
        </div>
        <van-divider>到底啦！</van-divider>
      </div>
    </div>
    <!-- 列表 end-->
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "Menu",
  data(){
    return {
      tabbarName: "classlist"
    }
  },

  created() {
    this.$store.state.vanTabbar = true;

    //初始化页面数据
    //获取banner图数据
    this.getBanner();

    //获取商品类型数据
    this.getType();
  },

  computed: {
    // bannerData图数据
    // typeData商品类型数据
    // left线条移动的距离
    // currentProduct当前类型商品数据
    ...mapState("menuModule", [
      "bannerData",
      "typeData",
      "left",
      "currentProduct"
    ])
  },

  methods: {
    //获取banner图
    getBanner() {
      this.axios({
        method: "GET",
        url: "/banner",

        //get请求携带的参数
        params: {
          appkey: this.appkey
        }
      })
        .then(result => {
          // 

          if (result.data.code == 300) {
            this.$store.commit("menuModule/changeData", {
              key: "bannerData",
              value: result.data.result
            });
          }

          // 
        })
        .catch(err => {
          
        });
    },

    //获取商品类型
    getType() {
      this.axios({
        method: "GET",
        url: "/type",

        //get请求携带的参数
        params: {
          appkey: this.appkey
        }
      })
        .then(result => {
          // 

          let types = result.data.result;

          //添加一个isActive, 用于判断激活标签
          types.forEach(v => {
            v.isActive = false;
          });

          // 向数组的开头添加一个或更多元素,并返回新的长度
          types.unshift({
            type: "isHot",
            typeDesc: "最新推荐",
            isActive: true
          });

          if (result.data.code == 400) {
            this.$store.commit("menuModule/changeData", {
              key: "typeData",
              value: types
            });

            //获取最新推荐商品数据
            this.getTypeProduct(this.typeData[0]);
          }

          // 
        })
        .catch(err => {
          
        });
    },

    //切换商品类型
    toggleType(item, index, e) {
      
      if (item.isActive) {
        return;
      }

      let types = this.typeData.concat();
      for (let i = 0; i < types.length; i++) {
        if (types[i].isActive) {
          types[i].isActive = false;
          break;
        }
      }

      //将当前激活
      types[index].isActive = true;

      this.$store.commit("menuModule/changeData", {
        key: "typeData",
        value: types
      });

      this.$store.commit("menuModule/changeData", {
        key: "left",
        value: index * e.target.clientWidth
      });

      this.getTypeProduct(item);
    },

    //获取商品类型的商品数据
    getTypeProduct(item) {
      // 
      //请求参数
      let params = {
        appkey: this.appkey
      };

      if (item.type == "isHot") {
        params.value = 1;
        params.key = "isHot";
      } else {
        params.value = item.type;
        params.key = "type";
      }

      this.axios({
        method: "GET",
        url: "/typeProducts",
        params
      })
        .then(result => {
          // 
          if (result.data.code == 500) {
            this.$store.commit("menuModule/changeData", {
              key: "currentProduct",
              value: result.data.result
            });

            // 
          }
        })
        .catch(err => {
          
        });
    },

    //携带商品pid跳转详情页
    goDetail(pid) {
      // this.$router.push({name: 'Details'})
      this.$router.push({ name: "Details", params: { pid } });
    }
  }
};
</script>

<style lang="less" scoped>
.classlist {
  .lits_top {
    text-align: center;
    background-color: #0197FE;
    margin-bottom: 10px;
    h3 {
    color: #fff;
    padding: 0;
    margin: 0;
    line-height: 40px;
    font-weight: 400;
    font-size: 18px;
    }
  }
  .swipe {
    padding: 0px 20px;
    img {
      width: 100%;
      background-size: cover;
      border-radius: 10px;
    }
  }
  .list {
    margin-bottom: 50px;
    .tab {
      text-align: center;
      line-height: 50px;
      color: #333;
      font-weight: 700;
      background-color: #fff;
      .line {
        bottom: 6px;
        width: 24%;
        height: 2px;
        background-color: rgba(96, 56, 17, 0.8);
        transition: left 0.2s;
        border-radius: 10px;
        margin-left: 1px;
      }
    }
    .shop_list {
      margin-top: 10px;
      .coffes_list {
        margin: 10px;
        padding: 5px;
        background-color: #f9f9f9;
        border-radius: 10px;
        img {
          width: 106px;
          border-radius: 10px;
        }
        h3 {
          color: #333;
          font-size: 17px;
          line-height: 2;
        }
        p {
          color: #999;
          font-size: 12px;
          line-height: 2;
        }
        span {
          color: #333;
          font-size: 14px;
          line-height: 2;
        }
      }
    }
  }
}
</style>